/*------------------------------------------------------------
Index
------------------------------------------------------------*/
._blank {
  position: relative;
  &:before {
    content: "";
    position: absolute;
    bottom: 2px;
    right: -13px;
    border-bottom: 1px solid #555;
    border-left: 1px solid #555;
    width: 6px;
    height: 6px;
  }
  &:after {
    content: "";
    position: absolute;
    bottom: 5px;
    right: -17px;
    border: 1px solid #555;
    width: 7px;
    height: 7px;
  }
}
.recipe {
  &-section {
    margin-top: 40px;
  }
  &__inner {
    background: url('../images/5.jpg') center center no-repeat;
    background-size: cover;
    margin-top: 40px;
  }
  &__calendar {
    width: 520px;
    .ttl {
      color: #fff;
      font-size: 22px;
      font-weight: bold;
      line-height: 2;
      display: flex;
      align-items: center;
      margin-bottom: 50px;
      img {
        float: left;
        margin-right: 25px;
      }
    }
  }
  &__list {
    width: 520px;
    padding: 60px 0 60px 20px;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    background: #fff;
    border: 5px solid #fff;
    margin-bottom: 10px;
    cursor: pointer;
    &:last-child {
      margin-bottom: 0;
    }
    .img--r {
      width: 225px;
      overflow: hidden;
      img {
        width: 100%;
        transition: all .3s;
      }
    }
    .detail--l {
      padding: 25px 20px 20px 30px;
      width: 310px;
      .ttl {
        color: #e4002b;
        font-size: 18px;
        font-weight: 500;
        border-bottom: 1px solid #e4002b;
        padding-bottom: 8px;
        margin-bottom: 12px;
      }
      .txt {
        font-size: 18px;
        font-weight: 500;
      }
    }
    &:hover {
      .img--r {
        img {
          transform: scale(1.02);
        }
      }
      .detail--l {
        .txt {
          text-decoration: underline;
          color: #e4002b;
        }
      }
    }
  }
}
.pick-up {
  margin: 60px 0 0 0;
  .box-l {
    width: 650px;
    .img {
      img {
        width: 100%;
      }
    }
  }
  .box-r {
    width: 503px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .item {
      width: 235px;
      margin-bottom: 70px;
      .img {
        height: 165px;
      }
      img {
        width: 100%;
      }
    }
  }
  .item {
    .img {
      img {
        transition: all .3s;
      }
    }
    &:hover {
      .img {
        img {
          transform: scale(1.02);
        }
      }
      .detail {
        .txt {
          text-decoration: underline;
          color: #e4002b;
        }
      }
    }
  }
  .img {
    margin-bottom: 20px;
    box-shadow: 0 0 80px #f2f2f2 inset;
    overflow: hidden;
  }
  .detail {
    .ttl {
      padding: 0 0 6px;
      padding-left: 30px;
      position: relative;
      border-bottom: 1px solid #e4002b;
      display: inline-block;
      padding-bottom: 10px;
      color: #e4002b;
      font-size: 16px;
      font-weight: 600;
      font-family: "Roboto", "YakuHanJP", "BaseFont-JP", Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", Meiryo, "ＭＳ Ｐゴシック", sans-serif;
      img {
        width: 70px;
      }
      &:after {
        content: "";
        background: url('../images/categoryicon_item_vegetables.png') left center no-repeat;
        position: absolute;
        background-size: cover;
        top: -7px;
        left: 0;
        width: 23px;
        height: 30px;
      }
      &.recipe-ttl {
        padding-left: 37px;
        &:after {
          width: 32px;
          height: 34px;
          background: url('../images/ico_gnav_recipes_book.svg') left center no-repeat;
        }
      }
    }
    .txt {
      font-size: 18px;
      margin-top: 5px;
    }
  }
}
.banner-pic-up {
  margin-top: 80px;
  .inner {
    width: 1040px;
    margin: 0 auto;
  }
  a {
    display: block;
    height: 120px;
    border: 1px solid #ddd;
    img {
      margin: -20px -70px 0;
      transition: all .3s;
    }
    &:hover {
      img {
        transform: scale(1.02);
      }
    }
  }
}
.product {
  &-section {
    margin-top: 100px;
  }
  &-mainSlide {
    margin-top: 40px;
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
    margin-top: 80px;
  }
  &__item {
    width: 25%;
    text-align: center;
    margin-bottom: 70px;
    img {
      width: 155px;
      transition: all .3s;
    }
    .caption {
      font-size: 14px;
      line-height: 1.5;
      color: #000;
      font-weight: 600;
      font-family: Roboto, YakuHanJP, BaseFont-JP, Helvetica, Arial, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", メイリオ, Meiryo, "ＭＳ Ｐゴシック", sans-serif;
    }
    &:hover {
      img {
        transform: scale(1.02);
      }
      .caption {
        color: #e4002b;
        text-decoration: underline;
      }
    }
  }
}
.entertaiment{
  &-section {
    margin-top: 80px;
    padding-top: 80px;
    border-top: 1px solid #ddd;
  }
  &-list {
    padding-top: 40px;
    padding-bottom: 30px;
  }
  &__item {
    width: 265px;
    .img {
      overflow: hidden;
      img {
        transition: all .3s;
      }
    }
    .ttl {
      color: #e4002b;
      display: inline-block;
      border-bottom: 1px solid #e4002b;
      font-size: 18px;
      padding-top: 10px;
      margin-bottom: 10px;
    }
    .txt {
      font-size: 18px;
      line-height: 1.7;
    }
    &:hover {
      .img {
        img {
          transform: scale(1.1);
        }
      }
      .txt {
        text-decoration: underline;
        color: #e4002b;
      }
    }
  }
}
.news {
  &-section {
    margin-top: 80px;
    padding-top: 80px;
    border-top: 1px solid #ddd;
  }
  &__list {
    width: 840px;
    margin: 40px auto 100px;
    border-bottom: 1px solid #ddd;
    li {
      border-top: 1px solid #ddd;
      a {
        padding: 15px 0;
        display: block;
      }
      &:hover {
        .title {
          color: #e4002b;
          text-decoration: underline;
        }
      }
    }
    .cat {
      background: #e4002b;
      color: #fff;
      font-size: 14px;
      max-width: 100px;
      width: 100%;
      padding: 5px 0;
      right: 5px;
      display: inline-block;
      text-align: center;
    }
    .time {
      color: #e4002b;
      margin-left: 20px;
    }
    .title {
      font-size: 16px;
      color: #000;
      margin-top: 10px;
    }
  }
}
